img {
    min-width: 100%;
    width: 100%;
}

.header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    border-bottom: 1px solid grey;
    padding: 13px 13px;
    margin-bottom: 24px;
    -ms-flex-pack: start;
        justify-content: flex-start;
}

.title {
    font-size: 20px;
    margin: 0;
}

.logo {
    height: 40px;
    width: 40px;
    background: #000;
    border-radius: 50%;
    margin-right: 10px;
}

.container {
    text-align: center;
    width: 680px;
    margin: auto;
}

.btn-new {
    color: #7fd323;
    text-transform: uppercase;
    background: transparent;
    border: 1px solid #7fd323;
    font-size: 20px;
    border-radius: 5px;
    font-weight: bold;
    height: 48px;
    width: 122px;
    cursor: pointer;
    margin-bottom: 21px;
}

.images-list {
    list-style: none;
    margin: 0;
    padding: 0;
}

.images-item {
    margin-bottom: 50px;
}

@media (max-width: 767px) {
    body {
        font-size: 16px;
    }

    .container {
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        width: 100%;
        padding: 0 20px;
    }

    .header {
        -ms-flex-pack: center;
            justify-content: center;
    }

    .logo {
        display: none;
    }

    .btn-new {
        width: 100%;
    }
}


